// src/Calculadora.js

import React, { useState } from 'react';
import './Calculadora.css';

const Calculadora = () => {
  const [input, setInput] = useState('');
  const [result, setResult] = useState('');
  const [theme, setTheme] = useState('light');

  const handleClick = (value) => {
    setInput(input + value);
  };

  const handleClear = () => {
    setInput('');
    setResult('');
  };
  
  const handleClearInput = () => {
    setInput(input.slice(0, -1)); // Elimina el ltimo carcter del input
  };
  

  const handleCalculate = () => {
    try {
      const evaluatedResult = eval(input); // eval puede ser peligroso, usa una alternativa ms segura en produccin
      setResult(evaluatedResult);
      setInput('');
      setResult(evaluatedResult);
    } catch (error) {
      setResult('Error');
    }
  };

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === 'light' ? 'dark' : 'light'));
  };

  return (
    <div className={`calculator-container ${theme}`}>
      <div className="calculadora">
        <div className="display">
          
          <input type="text" value={input} readOnly />
          <div className="result">{result}</div>
          <button className="btn clear" onClick={handleClearInput}> CE </button>
          <button className="btn clear" onClick={handleClear}>  C </button>
        </div>
        <div className="buttons">
          {['9', '8', '7', '/', '6', '5', '4', '*', '3', '2', '1', '+', '.', '0', '=', '-'].map((value, index) => (
            <button
              key={index}
              className={`btn ${['/', '*', '+', '-', '='].includes(value) ? 'operator' : ''} ${value === '.' ? 'decimal' : ''}`}
              onClick={value === '=' ? handleCalculate : () => handleClick(value)}
            >
              {value}
            </button>
          ))}
        </div>
      </div>
      <button className="theme-toggle" onClick={toggleTheme}>
        {theme === 'light' ? 'Tema Oscuro' : 'Tema Claro'}
      </button>
    </div>
  );
};

export default Calculadora;
